import Vue from 'vue'
import Vuex from 'vuex'
import mw from './mock.js'



  

Vue.use(Vuex)

var updateStore = function(store){
        store.subscribe(function(mutation, state){
          if(mutation.type === "START" || mutation.type === "SET_TERM" || mutation.type === "SET_FROM" || mutation.type === "SET_SELECTED" ){


if(mutation.type === "START"){

//create dates
    var today = new Date();
    var lastweek = new Date();
    lastweek.setDate( lastweek.getDate() - 7 );
    var lastmonth = new Date();
    lastmonth.setMonth( lastmonth.getMonth() - 1 );
    var lastquarter = new Date();
    lastquarter.setMonth( lastquarter.getMonth() - 4 );

   

    var realtoday = realdate(today);

    var date_ranges = [];
    var real_date_ranges = {};

    real_date_ranges["Last Week"] = {"from": realdate(lastweek), "to":  realtoday};
    real_date_ranges["Last Month"] = {"from": realdate(lastmonth), "to": realtoday };
    real_date_ranges["Last Quarter"] = {"from": realdate(lastquarter), "to": realtoday };

    for (var i=0; i < 5; i++) {
      var key = today.getFullYear() - i;
      real_date_ranges[key] = {"from": key + '-1-1' , "to": (key + 1) + '-1-1' };
    }

    Object.entries(real_date_ranges).forEach(function([key, value]){
      date_ranges.push({
        "key":key.toString(),
        "from":createdates(value.from),
        "to" :createdates(value.to)
      });
    })

    state.dates = date_ranges;
    state.realDates = real_date_ranges;







 


//get from url string

          var queryString = window.location.search;
          var urlParams = new URLSearchParams(queryString);
          var term = urlParams.get('term');
          var offset = urlParams.get('offset');
          var filters = urlParams.get('filters');
          if(term){
            state.term = term;
          }
          if(offset){
            state.from = offset;
          }
          if(filters){
            var urlfiltersout = [];
            var activefilters =  filters.split("~");
            activefilters.forEach(function(value) {
              var filteritem = value.split(".");
                var rangeitem = filteritem[0].split("_");
              if(rangeitem[0] == "range"){
                var ranges = filteritem[1].split("_");
              if(rangeitem[1] == "customrange"){
                Vue.set(state.realDates, "customrange", {"from": ranges[0], "to":  ranges[1]})

                
              }
              console.log(createdates(ranges[0]), ranges[0])
                state.rangeFrom = createdates(ranges[0]);
                state.rangeTo = createdates(ranges[1]);
                urlfiltersout.push({key:rangeitem[2], value:rangeitem[1], range: {"P:29.datField" : {gte: state.rangeFrom, lte:state.rangeTo } } });
              }else{
                urlfiltersout.push({value:filteritem[1], key: filteritem[0] });
            }
          });
            state.selected = urlfiltersout;
          }



}




            var root = state;
            root.loading = true;
            history.pushState({page: 1}, "title 1", urlstring() );
            var params = {
              action: 'query',
              meta: 'WSSearch',
              format: 'json',
              filter: JSON.stringify(root.selected),
              term:root.term,
              from:root.from,
              limit:root.size,
              pageid:mw.config.values.wgArticleId,
              dates:JSON.stringify(root.dates)
            },
            api = new mw.Api();

            api.post(  params ).done( function ( data ) {
              root.total = data.result.total;
              root.hits = JSON.parse(data.result.hits);
              root.aggs = data.result.aggs;
              root.loading = false;
            })
          }

          function urlstring(){
//create url string
          var url = "?";
          if(state.from > 0){
            url += "&offset="+ state.from;
          }

          if(state.term){
            url += "&term="+ state.term;
          }

          if(state.selected.length){
            url += "&filters=";
            var filtersarray = [];
            state.selected.forEach(function(item){
              if(item.range){
                  console.log(item.range['P:29.datField'].gte, state.realDates[item.value].from)
                filtersarray.push("range_"+item.value+"_"+item.key+"."+state.realDates[item.value].from+"_"+state.realDates[item.value].to);
              }else{
                filtersarray.push(""+item.key+"."+item.value+"");
              }
            });
            url += filtersarray.join("~");
          }
          return encodeURI(url);
         }

          function createdates(date){

                        function parseDate(str) {
                            return new Date(str)
                        }

                        function datediff(first, second) {
                            return Math.round((second-first)/(1000*60*60*24));
                        }

                        // 2451544 = 2000- 1- 1
                        var timestamp = 2451544;
                        return datediff(parseDate('2000-01-01'), parseDate(date)) + timestamp;
        }


        })
}

function realdate(date){
    return date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + date.getDate();
  }


var store = new Vuex.Store({
  state:{
    loading:false,
    selected:[],
    hits: "",
    aggs: "",
    total:0,
    from: 0,
    rangeFrom: 0,
    rangeTo: 0,
    term: "" ,
    loaded:false,
    dates:[],
    realDates:{},
  },
  mutations:{
     SET_SELECTED(state, selected){
       state.selected = selected;
     },
     SET_TERM(state, term){
       state.term = term
     },
     SET_FROM(state, from){
       state.from = from
     },
     START(state, start){
       state.loaded = start;
     },
     SET_RANGE_from(state, range){
          state.rangeFrom = range
     },
     SET_RANGE_to(state, range){
      state.rangeTo = range
     },
     SET_REAL_DATES(state, date){
        state.realDates = date
     }
  },
  getters:{
    rangeFrom(state){
       return state.rangeFrom;
     },
     rangeTo(state){
      return state.rangeTo;
    }
  },
  plugins: [updateStore]

})

export default store
