<template>
    <div  class="wssearch--pager">
        <span
          v-for="(pager, index) in pagers"
          :key="index"
          @click="next"
          :class="activepage(pager)"
        >
          <b 
            v-if="activepage(pager)"
          >
            {{pager}}
          </b>
          <span v-else>
            {{pager}}
          </span>
        </span>
      </div>
</template>

<script>
  export default {
    props:{
      total:Number,
      from:Number,
      size:Number
    },
    methods:{
        activepage(pager){
       if(pager == (this.from / this.size) + 1){
        return 'active';
       }
       return false
      },
         next(e){
            var from = 0;
            if(e.target.innerText.trim() == '<'){
               from = this.from - this.size;
            }else if(e.target.innerText.trim() == '>'){
               from = this.from + this.size;
            }else{
               from = Math.ceil(this.size * (e.target.innerText - 1));
            }
        this.$store.commit('SET_FROM', from)
    }
    },
    computed:{
      pagers(){
      if(this.total >= this.size){
        var pages = '';
        if(this.from == 0){
           pages = [];
        }else{
           pages = ['<'];
        }
        var i;
        var step = Math.ceil(this.total / this.size);
        for (i = 0; i < step; i++) {
          pages.push(i + 1)
        }
        if(this.from + this.size >=  this.total - this.size){
          console.log();
        }else{
          pages.push('>');
        }
        return pages;
      }
      return false
    }
    }
  }
</script>

<style>
.wssearch--pager {
  justify-self: end;
}
.wssearch--pager span {
  cursor: pointer;
  padding: 7px 4px;
}
.wssearch--pager span:hover {
  background: lightgray;
}
</style>