<template>
       <div
          :class="'wssearch--filter wssearch--filter__' + name.toLowerCase()"
        >
          <span 
            class="wssearch--filter-header" 
            v-if="buckets.length" 
          >
            <label>
              {{name.replace("_", " ")}}
            </label>
          </span>
          <div
           class="wssearch--filter-options"
          >
            <facet-checbox
              v-for="(agg, i) in bucketstoshow"
              :agg="agg"
              :key="i+agg.key+name"
              :index="i"
              :name="name"
            ></facet-checbox>
            <label
              class="wssearch--filter-showmore"
              v-if="bucketstoshow.length > 5"
              @click="more"
            >
              <span 
                :class="'wssearch--filter-'+lessormore" 
              >
                {{ $i18n( 'wssearch-' +lessormore ) }}
              </span>
            </label>
          </div>
        </div>
</template>

<script>
import FacetCheckbox from './FacetCheckbox'

  export default {
    components:{
      'facet-checbox':FacetCheckbox
    },
    props:{
      name:String,
      buckets:Array
    },
    data(){
      return {
        open:false
      }
    },
    methods:{
          more(){
           if(this.open){
              this.open = false
           }else{
              this.open = true
           }
        }
    },
    computed:{
       lessormore(){
         if(this.open){
           return 'less'
         }
         return 'more'
       },
        bucketstoshow(){
          if(this.open){
              return this.buckets
          }else{
          var bucks = this.buckets.filter(
            function(el){
              if(el.doc_count > 0){
                return el
              }
            }
          )
          return bucks;
        }
      }
    }
  }
</script>

<style >
  .wssearch--filter-showmore {
      display: block;
      width: 100%;
      height: 1em;
  }
</style>
